<template>
  <div id="petlist">
    <el-space direction="vertical" :size="20" style="width: 100%">
      <h1>浪浪管理</h1>
      <div style="text-align: right">
        <el-button type="primary" plain @click="onAddButtonClick">
          <el-icon><plus /></el-icon>
          添加浪浪資訊
        </el-button>
      </div>
      <!-- 浪浪列表 -->
        <el-table :data="pet_list" style="width: 100%">
          <el-table-column prop="pet_name" label="浪浪名字" width="180" />
          <el-table-column prop="pet_category" label="浪浪類別" width="180" />
          <el-table-column prop="pet_sex" label="浪浪性別" width="180" />
          <el-table-column prop="pet_variety" label="浪浪品種" width="180" />
          <el-table-column prop="pet_age" label="浪浪年齡" width="180" />
          <el-table-column prop="pet_location" label="浪浪所在地區" width="180" />
          <el-table-column prop="pet_date" label="收容日期" width="180" />
          <el-table-column prop="pet_build" label="浪浪體型" width="180" />
          <el-table-column fixed="right" label="編輯" width="250">
            <template #default="scope">
              <el-button type="danger" circle @click="onDeleteEvent(scope.$index)">
              <el-icon><delete /></el-icon>
              </el-button>

            </template>
          </el-table-column>
      </el-table>
      <div style="text-align: center;">
          <el-pagination 
          background 
          layout="prev, pager, next" 
          :total="total_count"
          :current_page="page"
          @current-change="onPageChanged" />
          </div>
    </el-space>
    <!-- 添加浪浪表單 -->
    <el-dialog v-model="PetsDialogVisible" title="添加浪浪資訊" width="50%">
      <el-form :model="form" :rules="rules" ref="dialogForm">
        <el-form-item label="浪浪名字" :label-width="formLabelWidth" required prop="pet_name">
          <el-input v-model="form.pet_name" autocomplete="off" />
        </el-form-item>
        <el-form-item label="浪浪類別" :label-width="formLabelWidth" required prop="pet_category">
          <el-select v-model="form.pet_category" placeholder="請選擇浪浪類別">
            <el-option label="狗勾" value="狗" />
            <el-option label="貓貓" value="貓" />
          </el-select>
        </el-form-item>
        <el-form-item label="浪浪性別" :label-width="formLabelWidth" required prop="pet_sex">
          <el-select v-model="form.pet_sex" placeholder="請選擇浪浪性別">
            <el-option label="男孩子" value="公" />
            <el-option label="女孩子" value="母" />
          </el-select>
        </el-form-item>
        <el-form-item label="浪浪品種" :label-width="formLabelWidth" required prop="pet_variety">
          <el-input v-model="form.pet_variety" autocomplete="off" />
        </el-form-item>
        <el-form-item label="收容日期" :label-width="formLabelWidth" required prop="pet_date">
          <el-input v-model="form.pet_date" autocomplete="off" />
        </el-form-item>
        <el-form-item label="浪浪年齡" :label-width="formLabelWidth" required prop="pet_age">
          <el-select v-model="form.pet_age" placeholder="請選擇浪浪年齡">
            <el-option label="成年(1歲(含)以上)" value="成年" />
            <el-option label="幼齡(1歲以下)" value="幼齡" />
          </el-select>
        </el-form-item>
        <el-form-item label="收容所地區" :label-width="formLabelWidth" required prop="pet_location">
          <el-input v-model="form.pet_location" autocomplete="off" />
        </el-form-item>
        <el-form-item label="毛色" :label-width="formLabelWidth" required prop="pet_coat">
          <el-input v-model="form.pet_coat" autocomplete="off" />
        </el-form-item>
        <el-form-item label="浪浪體型" :label-width="formLabelWidth" required prop="pet_build">
          <el-select v-model="form.pet_build" placeholder="請選擇浪浪體型">
            <el-option label="小型" value="小型" />
            <el-option label="中型" value="中型" />
            <el-option label="大型" value="大型" />
          </el-select>
        </el-form-item>
        <el-form-item label="是否結紮" :label-width="formLabelWidth" required prop="pet_TNR">
          <el-select v-model="form.pet_TNR" placeholder="請選擇是否結紮">
            <el-option label="是" value="已結紮" />
            <el-option label="否" value="未結紮" />
          </el-select>
        </el-form-item>
        <el-form-item label="浪浪照片網址" :label-width="formLabelWidth" required prop="pet_picture">
          <el-input v-model="form.pet_picture" autocomplete="off" />
        </el-form-item>
      </el-form>
      <template #footer>
        <span class="dialog-footer">
          <el-button @click="PetsDialogVisible = false">取消</el-button>
          <el-button type="primary" @click="onDialogSubmitEvent" 
            >確認</el-button>
        </span>
    </template>
  </el-dialog>

   <!-- 刪除浪浪確認框  -->
    <el-dialog
      v-model="deleteDialogVisible"
      title="提示"
      width="30%"
    >
      <span>您確定要刪除這隻浪浪嗎？</span>
      <template #footer>
        <span class="dialog-footer">
          <el-button @click="deleteDialogVisible = false">取消</el-button>
          <el-button type="primary" @click="onConfirmDeleteEvent">確定</el-button>
        </span>
      </template>
    </el-dialog>

  </div>
</template>

<script>
import { Plus,Delete } from "@element-plus/icons";
import {ElMessage} from "element-plus";
export default {
  name: "cms-petlist",
  components: {
    Plus,Delete
  },
  data(){
    return{
      PetsDialogVisible:false,
      deleteDialogVisible:false,
      deleteingIndex:0,
      pet_list:[],
      total_count:0,
      page:1,
      form:{
        pet_name:"",
        pet_category:"",
        pet_sex:"",
        pet_variety:"",
        pet_date:"",
        pet_age:"",
        pet_location:"",
        pet_coat:"",
        pet_build:"",
        pet_TNR:"",
        pet_picture:""
      },
      rules: {
        pet_name: [{required: true,message: '請輸入浪浪名字！',trigger: 'blur'}],
        pet_category: [{required: true,message: '請選擇浪浪類別！',trigger: 'blur'}],
        pet_sex: [{required: true,message: '請選擇浪浪性別！',trigger: 'blur'}],
        pet_variety: [{required: true,message: '請輸入浪浪品種！',trigger: 'blur'}],
        pet_date: [{required: true,message: '請輸入浪浪入所日期！',trigger: 'blur'}],
        pet_age: [{required: true,message: '請選擇浪浪年齡！',trigger: 'blur'}],
        pet_location: [{required: true,message: '請輸入浪浪所在收容所地區！',trigger: 'blur'}],
        pet_coat: [{required: true,message: '請輸入浪浪毛色！',trigger: 'blur'}],
        pet_build: [{required: true,message: '請選擇浪浪體型！',trigger: 'blur'}],
        pet_TNR: [{required: true,message: '請選擇浪浪是否結紮！',trigger: 'blur'}],
        pet_picture: [{required: true,message: '請附上浪浪照片！',trigger: 'blur'}],
      }
    }
  },
  mounted() {
        this.$http.getPetList().then((res) => {
          console.log(res.data)
          let data=res.data
          this.pet_list=data
          })
      },
  
  methods:{
  
    onAddButtonClick(){
      this.PetsDialogVisible=true;
      
    },
    onDialogSubmitEvent(){
      this.$refs["dialogForm"].validate((valid) =>
      {if(!valid){
        ElMessage.error("請確認是否輸入正確格式的內容!");
        return;
      }
      const form=this.form
        this.$http.addPet(form).then((result)=>{
          console.log(result)
          let code = result['status'];
          if(code === 200){
            let pet = result['data'];
            this.pet_list.push(pet);
            ElMessage.success("浪浪資訊添加成功!")
            this.PetsDialogVisible = false;
          }
        }).catch((err)=>{
          console.log(err);
          ElMessage.error(err);
          ElMessage.error("服務異常，請稍後再試");
          this.PetsDialogVisible = false;
        })
      })    
    },
    
    onDeleteEvent(index){
      this.deleteingIndex = index;
      this.deleteDialogVisible=true;
    },
    onConfirmDeleteEvent(){
      let pet = this.pet_list[this.deleteingIndex];
      this.$http.deletePet(pet.pet_id).then(res => {
        console.log(res)
        let result = res;
          let code = result['code'];
          if(code === 400){
            this.deleteDialogVisible = false;
            this.pet_list.splice(this.deleteingIndex,1);
            ElMessage.success("浪浪資訊刪除成功!")
          }
      })
    },
    onPageChanged(current_page){
      this.$http.getPetList(current_page)
    }
  }
};
</script>

<style scoped>
.el-space {
  display: block;
}
</style>