import { createRouter, createWebHashHistory } from "vue-router";
import CMSHome from "@/components/CMSHome.vue";
import CMSUser from "@/components/CMSUser.vue";
import CMSPet from "@/components/CMSPet.vue";


const routes = [
    {
        path: "/", component: CMSHome, name: "cms-home"
    },{
    path: "/cmspet", component: CMSPet, name: "cms-petlist"
},{
    path: "/cmsuser", component: CMSUser, name: "cms-userlist"
}];


const router = createRouter({
    history: createWebHashHistory(),
    routes, 
});

export default router;