<template>
  <div>
    <el-space direction="vertical" :size="20">
      <h1>用户管理</h1>
      <el-table :data="users" style="width: 100%">
        <el-table-column prop="username" label="用户名" width="250"/>
        <el-table-column prop="email" label="Email" width="250" />
        <el-table-column prop="join_time" label="加入時間" width="250" />
        <el-table-column label="管理員權限" width="250">
          <template #default="scope">
            <el-tag v-if="scope.row.is_staff">是</el-tag>
            <el-tag v-else type="danger">否</el-tag>
          </template>
        </el-table-column>
        <el-table-column label="用戶狀態" width="250">
          <template #default="scope">
            <el-tag v-if="scope.row.is_active" type="success">是</el-tag>
            <el-tag v-else type="danger">否</el-tag>
          </template>
        </el-table-column>
        <el-table-column label="編輯">
          <template #default="scope">
            <el-button
              type="danger"
              circle
              size="mini"
              @click="onActiveUserClick(scope.$index)"
            >
              <el-icon><delete /></el-icon>
            </el-button>
          </template>
        </el-table-column>
      </el-table>
    </el-space>

    <!-- 更改用戶權限提示對話框 -->
  <el-dialog
    v-model="confirmDialogVisible"
    title="提示"
    width="30%"
  >
    <span>{{message}}</span>
    <template #footer>
      <span class="dialog-footer">
        <el-button @click="confirmDialogVisible = false">取消</el-button>
        <el-button type="primary" @click="onConfirmActiveUserClick">確定</el-button>
      </span>
    </template>
  </el-dialog>
  </div>
</template>

<script>
// import axios from 'axios'
import {Delete} from "@element-plus/icons";
import { ElMessage } from 'element-plus';
export default {
    name: "cms-userlist",
    data(){
      return {
        confirmDialogVisible: false,
        users: [],
        activingIndex: 0,
        message: ""
      }
    },
    mounted(){
      this.$http.getUserList().then((res) => {
          console.log(res.data)
          let data=res.data
          this.users=data
          })
    },
    methods: {
      onActiveUserClick(index){
        this.activingIndex = index;
        this.confirmDialogVisible = true;
        const user = this.users[index];
        if(user.is_active){
          this.message = "您確定要封鎖此用户嗎？"
        }else{
          this.message = "您確定要取消封鎖此用户嗎？"
        }
      },
      onConfirmActiveUserClick(){
        let user = this.users[this.activingIndex];
        console.log(user.user_id)
        let is_active = user.is_active?0:1;
        this.$http.activeUser(user.user_id, is_active).then(res => {
          if(res && res['code'] == 200){
            ElMessage.success("編輯成功！");
            this.confirmDialogVisible = false;
            let user = res.data;
            this.users.splice(this.activingIndex, 1, user);
          }else{
            ElMessage.info("編輯失败！");
            this.confirmDialogVisible = false;
          }
        })
      }
    },
    components: {
      Delete
    }
}
</script>

<style scoped>
.el-space {
  display: block;
}
</style>