<script>
import {House,Postcard,User} from "@element-plus/icons"
export default {
  name: "App",
  components: {
    House,
    Postcard,
    User
  }, 
  computed: {
    defaultIndex(){
      // http://127.0.0.1:8080/#/post
      const path = this.$route.path;
      let index = "1";
      if(path.indexOf("cms-petlist") >= 0){
        index = "2"
      }  else if(path.indexOf('cms-userlist') >= 0){
        index = "3"
      }else{
        index = "1"
      }
      return index;
    }
  },
  mounted(){
    if(!this.$auth.is_staff){
      // window.location=this.$http.server_host;
    }
  },
  methods:{
    has_permission(permission){
      return this.$auth.user.permissions.indexOf(permission)>=0
    }
  }

}
</script>


