/* 網路請求封裝 */
import axios from "axios"
import auth from "./auth"
import qs from "qs"

// const SERVER_HOST="http://127.0.0.1:8000"
// const SERVER_HOST=window.location.origin;
// const SERVER_HOST = process.env.VUE_APP_SERVER_HOST

class Http {
	constructor() {
        // http://127.0.0.1:8000/petcms
			// this.server_host = SERVER_HOST;
		if(process.env.NODE_ENV == 'production'){
			this.server_host = window.location.origin;
		}else{
			this.server_host = "http://127.0.0.1:8000"
		}
		this.http = axios.create({ 
			baseURL: this.server_host + "/petcms",
			timeout: 1000*60
		});

        // 請求之前的攔截器，用来設置JWT(把token設置到headers中)
		this.http.interceptors.request.use( config => {
			const token = auth.token
			
			if (token) {
				config.headers.common.Authorization = "Bearer " + token
				console.log(token)
				
			}
			return config
		}) , function(e){print(e)}
		// response攔截
		//header  content
		this.http.interceptors.response.use( response => {
			return response.data;
		}), function(e){print(e)}
    }

    _post(url, data){
		return this.http.post(url, qs.stringify(data));
	}

	addPet(data){
		const url="/pet/add"
		return this._post(url,data);
	}

	getPetList(){
		const url="/pet/list"
		return this.http.get(url);
	}

	deletePet(pet_id){
		const url="pet/delete"
		return this._post(url,{"id":pet_id})
	}

	getUserList(){
		const url="/user/list"
		return this.http.get(url);

	}

	activeUser(user_id,is_active){
		const url="/user/active"
		return this._post(url,{"user_id":user_id,"is_active":is_active})
	}
}

export default new Http()